.movie {
    background-color: white;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #adaeb9;
    display: grid;
    grid-template-columns: 220px 1fr;
    grid-gap: 30px;
    text-decoration: none;
    color: inherit;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    font-style: oblique;
}

.movie_img {
    border: 2px solid lightgrey;
}

.movie_title {
    font-style: initial;
    font-size: larger;
    text-decoration: none;
    color: rgb(0, 0, 0);
}

.movie_summary, .movie_genres {
    font-size: large;
    font-weight: 600;
}